html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat-Regular"), local("Montserrat-Regular"), url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: local("Montserrat-Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: italic;
  font-weight: 500;
  src: local("Montserrat-Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: local("Montserrat-SemiBold"), local("Montserrat-SemiBold"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: italic;
  font-weight: 600;
  src: local("Montserrat-SemiBold"), local("Montserrat-SemiBold"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: local("Montserrat-Bold"), local("Montserrat-Bold"), url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo {
  width: 0.98em;
  height: 1em;
  fill: initial;
}

.icon-play {
  width: 1em;
  height: 1em;
  fill: initial;
}

body {
  font-family: "Montserrat", Open Sans, sans-serif;
}

.wrap {
  background-color: #00bdca;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow-x: hidden;
}

.wrap:before {
  content: '';
  position: absolute;
  top: 343px;
  width: 550%;
  height: 167%;
  background-color: #1d354e;
  transform: rotate(27deg);
  transform-origin: top left;
}

.wrap__cube-small {
  position: absolute;
  left: 6%;
  top: 26%;
}

.wrap__cube-blur {
  position: absolute;
  right: 9.5%;
  top: 39%;
  z-index: 5;
}

.in {
  max-width: 1030px;
  padding: 0 15px;
  margin: 0 auto;
}

.inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn {
  border-radius: 15px;
  background-color: #ffffff;
  color: #1d354e;
  padding: 14px 30px;
  font-size: 16px;
  font-weight: 600;
}

.lang {
  border-radius: 15px;
  background: #ffffff;
  font-size: 16px;
  font-weight: 600;
  padding: 15px 25px;
}

.lang__item:not(:last-child) {
  margin-bottom: 20px;
}

.lang__link {
  display: inline-block;
  text-decoration: none;
  color: #1d354e;
  border-bottom: 1px solid transparent;
}

.lang__link:hover {
  border-bottom: 1px solid #1d354e;
}

.title-lg {
  font-size: 72px;
  font-weight: 600;
  color: #ffffff;
  line-height: 1.2;
}

.title-md {
  font-size: 60px;
  font-weight: 600;
  color: #ffffff;
  line-height: 1.2;
}

.title-md_darken {
  color: #1d354e;
}

@media only screen and (max-width: 767px) {
  .title-md {
    font-size: 36px;
    text-align: center;
  }
}

.title-sm {
  font-size: 30px;
  line-height: 1.6;
  font-weight: 600;
  color: #ffffff;
}

.title-xs {
  font-size: 24px;
  font-weight: 600;
  color: #ffffff;
  line-height: 1.5;
}

.button {
  border-radius: 20px;
  background-color: #1d354e;
  color: #ffffff;
  font-size: 16px;
  font-weight: 400;
  padding: 18px 20px;
  min-width: 260px;
}

.col {
  float: left;
  padding: 0 10px;
}

.col-50 {
  width: 50%;
}

.col-33 {
  width: 33.3%;
}

@media only screen and (max-width: 1023px) {
  .col-md-33 {
    width: 33%;
  }
}

@media only screen and (max-width: 1023px) {
  .col-md-50 {
    width: 50%;
  }
}

@media only screen and (max-width: 767px) {
  .col-sm-33 {
    width: 33.3%;
  }
}

@media only screen and (max-width: 767px) {
  .col-sm-50 {
    width: 50%;
  }
}

@media only screen and (max-width: 767px) {
  .col-sm-100 {
    width: 100%;
  }
}

.header {
  padding: 65px 0;
  position: relative;
  z-index: 1;
}

.header__wrap:after {
  content: " ";
  display: table;
  clear: both;
}

.header__left {
  float: left;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header__right {
  float: right;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

.header__logo {
  margin-right: 10px;
}

.header__logo .icon-logo {
  fill: #ffffff;
  font-size: 45px;
}

.header__menu {
  margin-right: 30px;
}

.header__button {
  margin-right: 8px;
}

.header__logo-text {
  font-size: 24px;
  font-weight: 600;
  color: #ffffff;
  line-height: 1.2;
}

.header__logo-text_small {
  font-size: 13px;
  font-weight: 500;
}

.menu {
  font-size: 0;
  display: inline-block;
}

.menu__item {
  display: inline-block;
  font-size: 16px;
  font-weight: 600;
  padding: 15px 0;
}

.menu__item:not(:last-child) {
  margin-right: 35px;
}

.menu__link {
  color: #ffffff;
  display: block;
  text-decoration: none;
  font-family: "Montserrat", Open Sans, sans-serif;
}

.hero {
  padding: 35px 0 80px;
}

.hero__top {
  position: relative;
}

.hero__top:after {
  content: " ";
  display: table;
  clear: both;
}

.hero__img {
  position: absolute;
  top: -56%;
  left: -20%;
}

.hero__img img {
  display: block;
  max-width: 100%;
}

.hero__info {
  float: right;
  position: relative;
  max-width: 600px;
  text-align: center;
}

.hero__button {
  margin-top: 30px;
}

.hero__video {
  margin: 207px auto 0;
  max-width: 1210px;
  padding: 0 20px;
}

.video {
  position: relative;
}

.video__content {
  width: 100%;
  height: 725px;
  background: #000000;
  border: 50px solid #ffffff;
  border-radius: 16px;
}

.video__btn {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.video__btn .icon-play {
  fill: #00bdca;
  font-size: 123px;
}

.video__btn:before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 20px 0 20px 40px;
  border-color: transparent transparent transparent #ffffff;
}

.forecasts {
  position: relative;
  padding: 80px 0 65px;
}

.forecasts__title {
  margin-bottom: 18px;
}

.forecasts__subtitle {
  margin-bottom: 43px;
}

.forecasts__list {
  font-size: 0;
  display: inline-block;
  margin-bottom: 41px;
  margin: 0 -15px 41px;
}

@media only screen and (max-width: 767px) {
  .forecasts__list {
    margin: 0 -5px;
  }
}

.forecasts__item {
  display: inline-block;
  font-weight: 700;
  text-align: center;
  transition: background-color 0.3s, border 0.3s;
  padding: 0 15px;
}

@media only screen and (max-width: 1023px) {
  .forecasts__item {
    margin-bottom: 10px;
  }
}

@media only screen and (max-width: 767px) {
  .forecasts__item {
    padding: 0 5px;
  }
}

.forecasts__link {
  padding: 20px 10px;
  display: block;
  font-size: 30px;
  color: #ffffff;
  text-decoration: none;
  border-radius: 7px;
  border: 10px solid #223e5c;
  min-width: 140px;
}

.forecasts__link:hover {
  background-color: #00bdca;
  border: 10px solid #00bdca;
  border-radius: 7px;
  cursor: pointer;
}

@media only screen and (max-width: 767px) {
  .forecasts__link {
    min-width: 90px;
    font-size: 16px;
  }
}

.forecasts__img img {
  display: block;
  max-width: 100%;
}

.how-it-works {
  position: relative;
  padding-top: 65px;
}

.how-it-works__wrap {
  padding: 90px 100px 172px;
  background-color: #ffffff;
  border-radius: 15px;
}

@media only screen and (max-width: 767px) {
  .how-it-works__wrap {
    padding: 37px 10px;
  }
}

.how-it-works__title {
  margin-bottom: 50px;
}

.how-it-works__content {
  margin: 0 -10px;
}

.how-it-works__content:after {
  content: " ";
  display: table;
  clear: both;
}

.how-it-works__block {
  position: relative;
}

.how-it-works__block:not(:last-child) {
  margin-bottom: 30px;
}

.how-it-works__icon {
  position: absolute;
  top: 0;
  left: 0;
}

.how-it-works__description {
  padding-left: 80px;
  line-height: 2;
  font-size: 18px;
}
