.how-it-works {
	position: relative;
	padding-top: 65px;
	&__wrap {
		padding: 90px 100px 172px;
		background-color: $white;
		border-radius: 15px;
		@include sm {
			padding: 37px 10px; } }
	&__title {
		margin-bottom: 50px; }
	&__content {
		@include clr;
		margin: 0 -10px; }
	&__block {
		position: relative;
		&:not(:last-child) {
			margin-bottom: 30px; } }
	&__icon {
		position: absolute;
		top: 0;
		left: 0; }
	&__description {
		padding-left: 80px;
		line-height: 2;
		font-size: 18px; } }
